<div v-else-if="lang == 'Lojban'">
	<p> When people see the word <span class="font-weight-bold">logical</span> used to describe Nao, they often expect it to be similar to Lojban. However, the two languages have almost nothing in common. Nao is logical in the sense that it is based on a relatively small set of vocabulary and rules, and everything else logically derives from there with no exceptions or contradictions. It is logical in the sense that it relies on derivation rather than memorization. </p>

	<ol>
		<li>
			Lojban's main idea is to make sentences with a 100% unambiguous grammatical structure. Because of this, Lojban makes use of many markers and bracket-like words in order to ensure the complete clarity of the grammar tree. 

			Nao does not set that goal at all - the grammar ambuigity in Nao is on the same level as natural languages. But let's honestly ask ourselves - how often do people misinterpret the grammatical structure of sentences? Quite rarely. But the price for the unambiguous grammar is a large set of grammatical particles that are too complicated to learn for most people. That is why Nao does not have that feature.
		</li>
		<li>
			Lojban does not try to minimize the number of roots to memorize. For example, in Nao, the word for <i>mother</i> is <b>mipau</b> which literally means <i>female parent</i>. In Lojban it's <b>mamta</b> which is an elementary word, neither related to <i>parent</i> or <i>female</i> or anything else. Lojban doesn't connect the antonyms in any way, for example:
			<div class="pa-3">
				<b>glare</b> = hot/warm<br>
				<b>lenku</b> = cold/cool
			</div>

			Another example of words that are clearly semantically related but are independent in Lojban:
			<div  class="pa-3">
				<b>jmive</b> = alive<br>
				<b>jbena</b> = born<br>
				<b>morsi</b> = dead<br>
				<b>catra</b> = kill<br>
			</div>

			<p>And those are the obvious ones. And even when Lojban builds words such as lujvo, it's still hard because one needs to know rafsi - short forms of original words, and each word might have multiple rafsi. For example, <b>bangu</b> (language) might appear as <b>ban</b>, <b>bau</b> or <b>bang</b>. Moreover, lujvo compounds are not formed logically which means their meanings have to be guessed or memorized. </p>

			All of that means that it is a very big challenge to master Lojban vocabulary.
		</li>
	</ol>
</div>

<div v-else-if="lang == 'Tokipona'">
	<p> From the first glance it might seem like Nao and Tokipona are similar languages because both have a small number of elementary words. However, the difference between Nao and Tokipona is quite significant. </p>

	<ol>
		<li>
			In Tokipona, every word has a lot of meanings. Tokipona is deliberately a vague language, that is its feature. For example, <b>wile</b> can mean:
			<i>must, need, require, should, want, wish</i>. Tokipona is a very context-dependent language where you always have to rely on guessing. On the contrary, every word in Nao has one meaning. Nao aims to be a very precise and straightforward language, no guessing involved.
		</li><li>
			The vocabulary of Tokipona is limited to approximately 120 words. Nao, however, does not have any limitation to the number of elementary words. It just tries to avoid excessive roots. So knowing 300 elementary words in Nao is enough for most purposes.
		</li><li>
			Tokipona lacks the associativity property, which is actually an essential part of any agglutinative language. Associativity implies that if we have a compound consisting of three roots <i>x y z</i>, we should be able to extract <i>x y</i> and <i>y z</i> and both are expected to be correct compounds with the same meaning as in the original three-root compound. Nao has that important feature which allows for a much more powerful and convenient word construction system.
		</li><li>
			Word compounds look very different in two languages. The length of elementary words in Tokipona varies between 1 to 3 syllables and you must write them separately, often along with the marker <i>pi</i> in between. In Nao, every standard elementary word is 1 syllable long and thus you can just write them together with no spaces, which allows for a much higher semantic density.
		</li>
	</ol>
</div>

