function combine_number_and_noun(n, singular, two_to_four, five_to_ten) {
    let res;
    if (n > 10 && n < 20) res = five_to_ten;
    else if (n % 10 == 1) res = singular;
    else if ([2,3,4].includes(n % 10) ) res = two_to_four;
    else res = five_to_ten;

    return n + ' ' + res;
}

